// Import for variables from 'global-variables.css'
@import '../global-variables.scss';
// Import for mixins from 'global-mixins.css'
@import '../global-mixins.scss';


$max-option-block-width: 210px;

// Matrix
.matrix{
  
  .current-operator-display{
    text-Align: center;
    margin: 20px;
    font-Size: 3rem;
    color: $theme-color;
  }

  .operation-list{
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: max-content;
    margin:  auto;
    flex-wrap: wrap;
    .radio{
      margin: 10px;
      width: 50px;
      height: 50px;
    }
  }

  .base-matrices{
    @include desktop-res{
      position: relative;
      // background-color: grey;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-top: 40px;
      padding-bottom: 10px;
      // margin: auto;

      
    }
  }

  .matrix-wrapper{
    margin-top: 20px;

    .dimension-selections{
      display: flex;
      justify-content: space-between;
      margin: auto;
      max-width: $max-option-block-width;
    }

    .matrix-grid{
      margin: 40px auto 0 auto;
      max-width: 350px;
      display: flex;
      flex-wrap: wrap;
      .cell{
        display: flex;
        align-items: center;
        justify-content: center;;
        aspect-ratio: 1/1;
        background-color: $theme-background-solid;
        border-top: 3px solid $theme-color;
        border-left: 3px solid $theme-color;
        border-right: 3px solid $theme-color;
        
        .input{
          width: 100%;
          border: none;
          text-align: center;
          padding: 1em 0;
          font-size: 1.5rem;
          // font-size: 1.4rem;

          
          @include desktop-res{
            font-size: 2rem;
          }
        }
      }

    }
  }

  .equals{
    display: block;
    border-radius: 20px !important;
    padding: 1em 0;
    width: 100%;
    max-width: 200px;
    margin: 20px auto 0 auto;
  }
  &:has(.non-multiplication-mode){
    .dimension-selections{
      @include desktop-res{
        position: absolute;
        left: 50%;
        top: 20px;
        transform: translateX(-50%);
      }
    }
  }
}