// Import for variables from 'global-variables.css'
@import './global-variables.scss';
// Import for mixins from 'global-mixins.css'
@import './global-mixins.scss';

// .calculator-wrapper
.calculator-wrapper{
  @include theme-border;
  margin: auto;
  padding: 20px;
  margin-top: 100px;
  max-height: calc(100vh - 135px);
  max-width: 350px;
  overflow-y: scroll;
  transition: margin-left s$transition-time ease-in;

  .content{
    font-size: 1.5rem;
  }

  &:has(.classic){
    border: none;
    background-color: transparent;
    padding-top: 0;
  }

  &:has(.matrix, .temperature){
    max-width: 100%;
  }

  // .calculator-wrapper .greetings
  .greetings{
    text-align: center;
    *{
      padding: 10px 0;
    }
  }

  // .calculator-wrapper::after
  &::after{
    content: '';
    position: fixed;
    inset: 0;
    background: rgb(0, 0, 0);
    transition: all $transition-time ease-in;
  }

  // .calculator-wrapper.active
  &.active{
    // .calculator-wrapper.active::after
    &::after{
      opacity: 0;
      visibility: hidden;
    }
  }

  // .calculator-wrapper.inactive
  &.inactive{
    // .calculator-wrapper.active::after
    &::after{
      opacity: .5;
      visibility: visible;

      @include desktop-res{
        display: none;
      }
    }
  }

  *{
    color: white;
  }

  @include desktop-large-res{
    max-width: 400px;
  }
}