// Variables
$theme-color: #D28F0C;
$theme-color-darker: #9d6b06;
$theme-background-linear: linear-gradient(-135deg, #222222 0%, #1C1C1C 41%, #121212 100%);
$theme-background-linear-vertical: linear-gradient(180deg, #222222 0%, #1C1C1C 41%, #121212 100%);
$theme-background-solid: rgb(19, 19, 19);
$theme-shadow-color: #cd8c0a;
$button-transition: .1s;
$transition-time: .3s;
$button-background-transition: background-color $button-transition ease-in;

// Breakapoints
$min-tablet: 480px;
$min-desktop: 1024px;
$min-desktop-large: 2000px;
