// Import for variables from 'global-variables.css'
@import '../global-variables.scss';
// Import for mixins from 'global-mixins.css'
@import '../global-mixins.scss';

// .classic
.classic{
  @include theme-border;  
  padding: 1em;
  overflow: hidden;
  margin: auto;
  max-width: 340px;
  // .classic .output-input-data
  .output-input-data{
    margin-bottom: 10px;
    
    // .classic .output-input-data *
    .output{
      font-size: 1rem;
      text-align: right;
      padding: .5em 1em !important;
      width: 100%;
      background-color: $theme-color;
      margin: 0;
      padding: 0;
      
      &::selection{
        background-color: rgb(7, 7, 7);
        color: white;
      }
    }

    // tablet resolution for .classic .output-input-data
    @include tablet-res{
      .input,
      .output{
        font-size: 1.5rem;
      }
    }
  }

  // .classic .buttons
  .buttons{
    // .classic .buttons bdi
    bdi{
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      // gap: 5px;

      // .classic .buttons bdi .btn
      .btn{
        background-color: $theme-color;
        aspect-ratio: 1;
        border-radius: 50%;
        margin: 5px;
        font-size: 1.5rem;
        transition: $button-background-transition;

        // .classic .buttons bdi .btn:last-child
        &:last-child{
          grid-column: 3/5;
          aspect-ratio: 2;
          border-radius: 100px;
        }

        // .classic .buttons bdi .btn.number-btn
        &.number-btn{
          background: transparent;
          border-bottom: 3px solid $theme-color;
          position: relative;
          overflow: hidden;
          transition: border-color .1s ease-in;

          // .classic .buttons bdi .btn::before
          &:before{
            content: '';
            border-radius: 50%;
            position: absolute;
            inset: auto 0 0 0;
            height: 0%;
            background-color: $theme-color;
            transition: height .1s ease-in;
          }

          // .classic .buttons bdi .btn:hover
          &:hover{
            background-color: transparent;
            border-color: transparent;

            // .classic .buttons bdi .btn:hover::befores
            &::before{
              height: 40%;
            }
          }
        }

        &:hover{
          background-color: $theme-color-darker;
        }

        // tablet resolution for .classic .buttons bdi .btn
        @include tablet-res{
          font-size: 1.5rem;
        }
      }

    }
  }
  // large desktop resolution for .classic
  @include desktop-large-res{
    max-width: 500px;
  }
}