@import './global-variables.scss';

@mixin box-shadow($x, $y, $blur, $spread, $shadow-color){
  -webkit-box-shadow: $x $y $blur $spread $shadow-color;
  -moz-box-shadow: $x $y $blur $spread $shadow-color;
  box-shadow: $x $y $blur $spread $shadow-color;
}

@mixin center-on-raw-position{
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin theme-background{
  background: rgb(17,17,17);
  background: linear-gradient(190deg, rgba(17,17,17,1) 0%, rgb(22, 22, 22) 70%, rgb(39, 39, 39) 100%);
}

@mixin radio-scale($value){
  -ms-transform: scale($value); /* IE 9 */
  -webkit-transform: scale($value); /* Chrome, Safari, Opera */
  transform: scale($value);
}

// Aplpy custom style to all headings
@mixin all-text{
  @for $i from 1 through 6 {
    h#{$i}{
      @content;
    }
  }
  p,a{
    @content;
  }
}

// Apply theme border to element
@mixin theme-border{
  background-color: $theme-background-solid;
  border-right: 1px solid $theme-color;
  border-bottom: 3px solid $theme-color;
  border-radius: 20px 0 20px 0;
}

// Media queries
@mixin tablet-res{
  @media screen and (min-width: $min-tablet) {
    @content;
  }
}

@mixin desktop-res{
  @media screen and (min-width: $min-desktop) {
    @content;
  }
}
@mixin desktop-large-res{
  @media screen and (min-width: $min-desktop-large) {
    @content;
  }
}