// Import for variables from 'global-variables.css'
@import './global-variables.scss';
// Import for mixins from 'global-mixins.css'
@import './global-mixins.scss';

.popup{
  position: fixed;
  top: 40px;
  left: 50%;
  text-align: center;
  font-size: .9rem;
  padding: 1em 2em;
  font-weight: bold;
  border-radius: 100px;
  @include box-shadow(0px, 12px, 15px, -11px, rgb(14, 14, 14));
  transform: translateX(-50%);
  background-color: $theme-color;
  color: white;
  z-index: 100;
  user-select: none;
  animation: appear .5s forwards, .5s disappear 3s forwards;

  @include tablet-res{
    font-size: 2rem;
  }
}

@keyframes appear{
  0%{
    opacity: 0;
    top: 0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes disappear{
  0%{
    opacity: 1;
  }
  100%{
    opacity: 0;
    top: 60px;
  }
}