// Import for variables from 'global-variables.css'
@import './global-variables.scss';
// Import for mixins from 'global-mixins.css'
@import './global-mixins.scss';

*,
*::after,
*::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: transparent !important;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: $theme-color;
  border-radius: 0 20px 20px 0;

  
  @include tablet-res{
    border-radius: 20px 0 30px 0;
  }
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: #9d6b06;
}

// .App
.App{
  min-height: 100vh;
  padding: 1rem;
  overflow: hidden;
  @include theme-background;
  
  // .App *
  @include all-text{
    color: white;
  }

  *::selection{
    background-color: $theme-color;
    color: rgb(0, 0, 0);
  }

  .select-wrap{
    @include theme-border;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $theme-color-darker;
    width: max-content;
    padding: 0 .5em;
    .select{
      cursor: pointer;
      font-size: 1.5rem;
      padding: .5em;
      outline: none;
      background-color: $theme-color-darker;
      border: none;
    }
  }

  .input{
    text-align: right;
    @include theme-border;
    width: 100%;
    padding: 1em;
    font-size: 1.2rem;
    font-weight: bold;
    color: $theme-color;

    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button{
      -webkit-appearance: none;

    }

    &::placeholder{
      color: $theme-color-darker;
    }

    // Firefox
    &[type=number] {
      -moz-appearance: textfield;
    }
  }

  .radio{
    background: transparent;
    position: relative;
    width: 2rem;
    height: 2rem;
    font-size: 1.2rem;
    font-weight: bold;
    color: $theme-color;
    cursor: pointer;
    // transition: ;
    
    &::after{
      @include center-on-raw-position;
      position: absolute;
      content: '';
      width: 110%;
      height: 110%;
      background-color: $theme-background-solid;
      border-bottom: 3px solid $theme-color;
      border-radius: 50%;
      transition: border $button-transition linear;
    }

    &::before{
      @include center-on-raw-position;
      position: absolute;
      content: attr(data-display);
      z-index: 1;
    }

    &:checked::after{
      background-color: $theme-color;
    }

    &:hover::after{
      border-bottom-width: 20px ;
    }

    &:checked{
      color: white;
    }
  }

  .negative-selection{
    *::selection{
      background-color: white;
      color: white;
    }
  }

  .title{
    font-size: 2rem;
    background-color: $theme-color;
    width: max-content;
    margin: auto;
    border-radius: 20px;
    padding: .3em;
  }

  // .App ul,ol
  li,ol{
    list-style: none;
  }

  button{
    cursor: pointer;
    border: none;

    &.btn{
      background-color: $theme-color;
      border-radius: 50%;
      font-size: 1rem;
      transition: $button-background-transition;

      &:hover{
        background-color: $theme-color-darker;
      }
    }
  }

  hr{
    border-top: 1px solid $theme-color;
    border-bottom: 2px solid $theme-color-darker;
    max-width: 400px;
    margin: 20px auto;
  }

  a{
    text-decoration: none;
    color: $theme-color;
  }

  input{
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
  }

  .underscored{
    text-decoration: underline;
  }

  .important{
    color: $theme-color !important;
  }

  .loading{
    position: relative;
    overflow: hidden;
    
    &::after{
      position: absolute;
      content: '';
      inset: 0;
      background: rgba(128, 128, 128, 0.122);
    }
  }

  @include desktop-res{
    padding-left: 300px;
  }
}