// Import for variables from 'global-variables.css'
@import './global-variables.scss';
// Import for mixins from 'global-mixins.css'
@import './global-mixins.scss';

// .sidebar
.sidebar{
  @include theme-background;
  @include box-shadow(15px, 8px, 24px, -5px, transparent);
  position: fixed;
  display: flex;
  justify-content: center;
  left: 0;
  top: 0;
  height: 100%;
  width: 0;
  max-width: 100%;
  overflow-x: hidden;
  transition: all $transition-time ease-in-out;
  z-index: 10;
  
  // .sidebar .navigation-nav
  .navigation-nav{
    width: 100%;
    max-width: 300px;
    margin: 1em 0;
    padding: 0 1em;
    margin-top: 100px;
    overflow-y: scroll;
    overflow-x: hidden;
    direction: rtl;

    // .sidebar .navigation.nav list
    .list{
      text-align: center;
      overflow: hidden;
      direction: ltr;

      // .navigation-nav .list .list-item
      .list-item{
        position: relative;
        font-size: 1.5rem;
        margin: 5px 0;
        overflow: hidden;
        z-index: 10;

        // .navigation-nav .list .list-item a
        a{
          color: white;
          display: block;
          border-bottom: 1px solid $theme-color;
          padding: 1em 0;
          width: 100%;
        }

        // .navigation-nav .list .list-item.selected
        &.selected{
          background-color: $theme-color;
          a{
            color: white;
          }
        }

        // .navigation-nav .list .list-item:hover
        &:hover{
          &:after{
            content: '';
            position: absolute;
            inset: auto 20% 0 20%;
            height: 0px;
            @include box-shadow(0px, -8px, 20px, 6px, $theme-color);
          }
        }
      }

      // tablet resolution for .navigation-nav .list
      @include tablet-res{
        direction: ltr;
      }
    }

    // tablet resolution for .navigation-nav .list
    @include tablet-res{
      direction: ltr;
    }
  }

  // .sidebar .toggle
  .toggle{
    color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $theme-color;
    font-size: 1.5rem;
    z-index: 100;
    transition: $button-background-transition;
    // .sidebar .toggle
    &:hover{
      background-color: $theme-color-darker;
    }

    // desktop resolution for .siedbar .toggle
    @include desktop-res{
      display: none;
    }
  }

  // .sidebar.active
  &.open{
    @include box-shadow(0px, 8px, 20px, 0px, $theme-shadow-color);
    width: 100%;
  }

  // .sidebar *
  *{
    user-select: none;
    -webkit-user-drag: none;
  }

  // tablet resolution for .sidebar
  @include tablet-res{
    max-width: 300px;
  }

  // desktop resolution resolution for .sidebar
  @include desktop-res{
    width: 300px !important;
    @include box-shadow(0px, 8px, 20px, 0px, $theme-shadow-color);
  }
}